 /* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* CSS reset end */



/* base */
*{
    box-sizing: border-box;
}
body{
    font-family: 'Noto Sans TC', sans-serif;;
}
img{
    display: block;
}
.wrap{
    max-width: 1200px;
    margin: 0 auto;
}
.color-primary{
    color: #71b6b4;
}
.color-secondary{
    color: #888;
}
@media (max-width:992px){
    .wrap{
        width: 100%;
        padding: 0 20px;
    }
}
@media (max-width:767px){
    .wrap{
        padding: 0 15px;
    }
}


.header{
    display: flex;
    flex-wrap: wrap;
    margin-top: 48px;
    margin-bottom: 60px;
}
.pic{
    margin-right: 48px;
}
.name{
    font-family: 'Roboto', sans-serif;
    font-size: 80px;
    font-weight: 400;
    line-height: 106px;
}
.intro{
    font-size: 20px;
    font-weight: 400;
    line-height: 36px;
}
.tittle{
    font-size: 32px;
    font-weight: 400;
    line-height: 43px;
    padding-bottom: 8px;
    border-bottom: 1px solid #000;
    margin-bottom: 16px;
    /* span內英文垂直置中 */
    display: flex;
    align-items: center;
}
.en{
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    margin-left: 16px;
}
.info{
    margin-bottom: 72px;
}
.info li{
    font-size: 24px;
    font-weight: 400;
    line-height: 36px;
    margin-bottom: 24px;
}
@media (max-width:767px) {
    .header{
        justify-content: center;
        margin-bottom: 30px;
    }
    .pic{
        max-width:70%;
        margin-right: 36px;
    }
    .name{
        font-size: 40px;
        line-height: 60px;
    }
    .intro{
        font-size: 16px;
        line-height: 24px;
    }
    .tittle{
        font-size: 24px;
        line-height: 32px;
    }
    .en{
        font-size: 20px;
    }
    .info{
        margin-bottom: 36px;
    }
    .info li{
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 12px;
    }
}


.exp{
    margin-left: 24px;
    border-left: 1px solid #71b6b4;
    padding-left: 24px;
}
.exp li{
    margin-bottom: 48px;
}
.exp p{
    font-size: 20px;
    line-height: 30px;
}
.exp h3{
    margin-bottom: 12px;
}
.mt-1{
    margin-top: 16px;
    font-size: 20px;
    line-height: 30px;
}
@media (max-width:767px){
    .exp{
        margin-left: 12px;
        padding-left: 12px;
    }
    .exp p{
        font-size: 16px;
        line-height: 24px;
    }
}



.skill-area{
    background-color: #71b6b4;
    padding-top: 24px;
    padding-bottom: 36px;
    margin-bottom: 72px;
}
.title-skill{
    max-width: 996px;
    margin: 0 auto;
    color: #FFF;
    font-size: 32px;
    font-weight: 400;
    line-height: 45px;
    text-align: center;
    padding-bottom: 8px;
    border-bottom: 1px solid #FFF;
    margin-bottom: 37px;
    /* span內英文垂直置中 */
    display: flex;
    align-items: center;
    justify-content: center;
}
.skill{
    width: 250px;
    margin: 0 auto;
    padding-left: 24px;
    border-left: 1px solid #FFF;
}
.skill li{
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
    font-weight: 300;
    line-height: 36px;
    color: #FFF;
    margin-bottom: 16px;
}
@media (max-width:992px) {
    .title-skill{
        width: 90%;
    }
}
@media (max-width:767px){
    .title-skill{
        font-size: 24px;
        line-height: 32px;
    }
    .skill li{
        font-size: 18px;
        line-height: 24px;
    }
    .skill-area{
        margin-bottom: 36px;
    }
}



.school{
    margin-top: 12px;
    margin-left: 20px;
}
.school li{
    font-size: 16px;
    line-height: 18px;
    color: #999;
}




footer{
    background-color: #71b6b4;
    text-align: center;
    color: #FFF;
    padding: 24px 0 86px 0;
}
.title-contact{
    font-family: 'Noto Sans TC', sans-serif;
    font-size: 32px;
    font-weight: 400;
    line-height: 45px;
    padding-bottom: 8px;
    border-bottom: 1px solid #FFF;
    margin-bottom: 32px;
    /* span內英文垂直置中 */
    display: flex;
    align-items: center;
    justify-content: center;
}
.contact li{
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
    font-weight: 300;
    line-height: 36px;
    margin-bottom: 16px;
}
.contact li a{
    text-decoration: none;
    color: #FFF;
}
.contact li a:hover{
    color: #e0fffe;
}
.icon{
    display: flex;
    justify-content: center;
    padding-top: 20px;
}
.icon li{
    padding: 0 12px;
}
.icon li a img:hover{
    opacity: 0.8;
}
@media (max-width:767px){
    .title-contact{
        font-size: 24px;
        line-height: 32px;
    }
    .contact li{
        font-size: 14px;
        line-height: 21px;
    }
}